acial; en segundo lugar, la demanda espacial de los servicios de transporte y, en tercer lugar, los sistemas surgidos en la compleja dialéctica (redes-demanda-espacio). Desde un punto de vista funcional y sistémico, las redes de transporte constituyen el sistema arterial de la organización regional. Están definidas por la estructura de las rutas de carreteras, ferrocarriles o los corredores marítimos y aéreos, entendiéndose como ruta la simple relación o arco entre los nodos que son parte de la red. Los nodos constituyen los focos de generación y atracción de flujos, estos últimos, los elementos transportados a través de las redes (personas, mercancías, información) Sin embargo, el sector económico es importante en el desarrollo del transporte en todas sus esferas. (p.161-174)")])])])]),_c('div',{staticClass:"bloque-texto-d__autor"},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-11"},[_c('div',{staticClass:"h6 mb-0"},[_vm._v("Para Martínez, F (2009)")])]),_c('div',{staticClass:"col-1"},[_c('i',{staticClass:"fas fa-quote-right"})])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-6 p-4 align-self-center"},[_c('div',{staticClass:"p mb-4"},[_vm._v("Los modos de transporte son los sistemas para la movilización, utilizados en el traslado de la mercancía desde el punto de origen al punto de destino. Combinan redes, vehículos y operaciones. Los medios de transporte hacen referencia al tipo de vehículos requeridos para la transportación de acuerdo al modo escogido (avión, barco, camión y ferrocarril).")]),_c('div',{staticClass:"p"},[_vm._v("En la siguiente tabla podemos observar cuáles son los modos y medios de transporte y qué ventajas ofrece cada uno de ellos.")])]),_c('div',{staticClass:"col-lg-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/img5.svg"),"alt":"Medios y Modos de Transporte"}})])])])},function () {var _vm=this;var _h=_vm.$cre